<template>
	<div class="page1_3_1">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
		</div>
		<div>
			<div class="form box-border-shadow">
				<label class="form-label">分类标准选择:</label>
				<el-select v-model="select1" clearable placeholder="来源">
				    <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
				    </el-option>
				</el-select>
				<el-select v-model="select2" clearable placeholder="形式">
				    <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
				    </el-option>
				</el-select>
				<el-select v-model="select3" clearable placeholder="主题">
				    <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
				    </el-option>
				</el-select>
				<!-- <el-input class="textarea" type="textarea" :autosize="{ minRows: 2, maxRows: 10}" placeholder="请输入内容" v-model="textarea">
				</el-input> -->
				<div class="upload-demo" style="margin: 20px 0;">
					<el-upload
					    class="upload-demo"
					    drag
					    action=""
					    :http-request="onload"
					    :show-file-list="false"
					    :before-upload="beforeAvatarUpload"
					     multiple>
					    <i class="el-icon-upload"></i>
					    <div class="el-upload__text">
					    	<div>将文件拖到此处，或<em>点击上传</em></div>
					    	<div>{{file.name}}</div>
					    </div>
					</el-upload>
					<!-- <div tabindex="0" class="el-upload el-upload--text" style="position: relative;">
						<div class="el-upload-dragger">
							<i class="el-icon-upload"></i> 
							<div class="el-upload__text">
								<em v-if="file===''">点击上传财务指标相关文件</em>
								<span v-else>{{file.name}}</span>
							</div> 
						</div>
						<input type="file" name="file" multiple="multiple" class="el-upload__input" @change="uploading($event)">
					</div> -->
					<div class="el-upload__tip">只能上传txt文件，请设置为UTF-8编码</div>
				</div>
				 <el-button type="primary" plain @click="submit($event)">提交文档</el-button>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import reqData from "../../../reqData.js"
	export default{
		name:'page1_3_1',
		data(){
			return{
				msg:["审计准备","审计数据及维度","财务指标"],
				options1: [{
		          value: '内部数据',
		          label: '内部数据'
		        }, {
		          value: '外部数据',
		          label: '外部数据'
		        }, {
		          value: '网络数据',
		          label: '网络数据'
		        }],
		        options2: [{
		          value: '结构化',
		          label: '结构化'
		        }, {
		          value: '非结构化',
		          label: '非结构化'
		        }, {
		          value: '半结构化',
		          label: '半结构化'
		        }],
		        options3: [{
		          value: '资环',
		          label: '资环'
		        }, {
		          value: '社保',
		          label: '社保'
		        }, {
		          value: '金融',
		          label: '金融'
		        }],
		        select1: '',
		        select2: '',
		        select3: '',
		        // textarea:'',
		        file:''
			}
		},
		components:{
			Place
		},
		methods: {
			onload(){
	       		// this.imgUrl=window.URL.createObjectURL(this.file);
	       		// console.log(window.URL.createObjectURL(this.file)); 
	       },
	        beforeAvatarUpload(file) {
		        console.log(file);
		        // this.imgUrl = URL.createObjectURL(file.raw);
		        this.file=file;
	       },
		   submit(event){
			   	if(this.select1===''||this.select2===''||this.select3===''){
			   		alert('请先选择分类标准')
			   	}else if(this.file===''){
			   		alert('请先上传文档')
			   	}else{
			   		event.preventDefault();//取消默认行为
				  	let formdata = new FormData();
				  	formdata.append('file',this.file);
					formdata.append('type1',this.select1);
					formdata.append('type2',this.select2);
					formdata.append('type3',this.select3);
					console.log(this.file);
					console.log(formdata);
				  	let config = {
			            headers: {
			                'Content-Type': 'multipart/form-data;charset=utf-8'  //之前说的以表单传数据的格式来传递fromdata
			            }
			        };
			        this.$axios.post('http://121.42.29.188:10000/file_text', formdata, config)
					.then( (res) => {
			            //做处理
						this.select1==='';
						this.select2==='';
						this.select2==='';
						this.file==='';
						console.log(res);
						alert("提交成功")
		            }).catch((error) =>{
						alert("提交成功")
					});
			   	}
			  	
		  	}
	    }
	
	}
</script>
<style type="text/css" scoped="">
	.form{
		text-align: center; 
		margin: 20px auto;
		min-height: 600px;
		padding: 30px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
	.el-select{
		width: 25%;
	}
	.textarea{
		width: 60%;
		margin: 20px auto;
	}
	.el-upload {
	    width: 50%;
	}
	.el-upload-dragger{
		width: 100%;
	}
	.el-upload__input {
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    display: block;
	    left: 0;
	    right: 0;
	    width: 100%;
	    opacity: 0;
	    cursor: pointer;
	}
	.el-upload__input:hover{
		border:1px solid #ccc;
	}
	.el-upload-dragger .el-upload__text em {
	    color: #b80000;
	}
	.el-button--primary.is-plain {
	    color: #b80000;
	    background: #fff2f2;
	    border-color: #b80000;
	}
	.el-button--primary.is-plain:hover {
	    background: #b80000;
	    border-color: #b80000;
	    color: #fff;
	}
</style>


